import React from 'react';
import {FaFacebookSquare , FaInstagram , FaLinkedin , FaYoutube , FaMobileAlt  , FaEnvelope } from 'react-icons/fa';
import './Slider/Slider.css';

const Slider = () => { 
   // console.log(data)
    return(
        <div className='Slider' >
    <div class='csslider1 autoplay'>
    <input name="cs_anchor1" id="cs_slide1_0" type="radio" class="cs_anchor slide"/>
    <input name="cs_anchor1" id="cs_slide1_1" type="radio" class="cs_anchor slide"/>
    <input name="cs_anchor1" id="cs_slide1_2" type="radio" class="cs_anchor slide"/>
    <input name="cs_anchor1" id="cs_slide1_3" type="radio" class="cs_anchor slide"/>
    <input name="cs_anchor1" id="cs_play1" type="radio" class="cs_anchor" />
    <input name="cs_anchor1" id="cs_pause1" type="radio" class="cs_anchor"/>
    <ul>
        <div style="width: 100%; visibility: hidden; font-size: 0px; line-height: 0;">
            <img src="../../static/images/buns.jpg" />
        </div>
        <li class="num0 img">
            <img src="../../static/images/buns.jpg" alt="Buns" title="Buns"/>
        </li>
        <li class="num1 img">
            <img src="../../static/images/croissant.jpg" alt="Croissant" title="Croissant"/>
        </li>
        <li class="num2 img">
            <img src="../../static/images/lemonpie.jpg" alt="Lemon pie" title="Lemon pie"/>
        </li>
        <li class="num3 img">
            <img src="../../static/images/teaandcake.jpg" alt="Breakfast" title="Breakfast"/>
        </li>
    
    </ul>
    <div class="cs_description">
        <label class="num0">
            <span class="cs_title"><span class="cs_wrapper">Buns</span></span>
            <br/><span class="cs_descr"><span class="cs_wrapper">Curd buns</span></span>
        </label>
        <label class="num1">
            <span class="cs_title"><span class="cs_wrapper">Croissant</span></span>
            <br/><span class="cs_descr"><span class="cs_wrapper">Chocolate croissant</span></span>
        </label>
        <label class="num2">
            <span class="cs_title"><span class="cs_wrapper">Lemon pie</span></span>
            <br/><span class="cs_descr"><span class="cs_wrapper">Two pieces of lemon pie</span></span>
        </label>
        <label class="num3">
            <span class="cs_title"><span class="cs_wrapper">Breakfast</span></span>
            <br/><span class="cs_descr"><span class="cs_wrapper">Chocolate croissant</span></span>
        </label>
    </div>
    
    <div class="cs_arrowprev">
        <label class="num0" htmlFor="cs_slide1_0"></label>
        <label class="num1" htmlFor="cs_slide1_1"></label>
        <label class="num2" htmlFor="cs_slide1_2"></label>
        <label class="num3" htmlFor="cs_slide1_3"></label>
    </div>
    <div class="cs_arrownext">
        <label class="num0" htmlFor="cs_slide1_0"></label>
        <label class="num1" htmlFor="cs_slide1_1"></label>
        <label class="num2" htmlFor="cs_slide1_2"></label>
        <label class="num3" htmlFor="cs_slide1_3"></label>
    </div>
    
    <div class="cs_bullets">
        <label class="num0" htmlFor="cs_slide1_0">
            <span class="cs_point"></span>
            <span class="cs_thumb"><img src="../../static/images/buns.jpg" alt="Buns" title="Buns"/></span>
        </label>
        <label class="num1" htmlFor="cs_slide1_1">
            <span class="cs_point"></span>
            <span class="cs_thumb"><img src="../../static/images/croissant.jpg" alt="Croissant" title="Croissant"/></span>
        </label>
        <label class="num2" htmlFor="cs_slide1_2">
            <span class="cs_point"></span>
            <span class="cs_thumb"><img src="../../static/images/lemonpie.jpg" alt="Lemon pie" title="Lemon pie"/></span>
        </label>
        <label class="num3" htmlFor="cs_slide1_3">
            <span class="cs_point"></span>
            <span class="cs_thumb"><img src="../../static/images/teaandcake.jpg" alt="Breakfast" title="Breakfast"/></span>
        </label>
    </div>
</div>

             
                
        </div>
    );
}

export default Slider;


